body {
  font-family: $font-family--poppins;
  color: $black-opopacity-01;
  font-weight: 500;
  font-size:  16px;
  line-height: 21px;
  min-height: 100vh;
  min-width: 320px;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  overflow-x: hidden;
}

.body__fixed-page {
  overflow: hidden;
}
